@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");

/* Global Styling */

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary-font-color: #003c83;
  --secondary-font-color: #7289a3;
  --black-font-color: #323338;
  --green-color: #008e4a;
  --orange-color: #ffa53a;
  --yellow-color: #ffeb3a;
  --red-color: #ff3a3a;
  --pink-color: #f74875;
  --blue-color: #0073ea;
  --light-blue-color: #ebf5ff;
  --h1-size: 2rem;
  --h2-size: 1.8rem;
  --h3-size: 1.6rem;
  --h4-size: 1.4rem;
  --h5-size: 1.2rem;
  --h6-size: 1.5rem;
  --border: 1px solid #eeeeee;
}

html {
  font-size: 62.5%;
  /* 1rem = 10px */
  scroll-behavior: smooth;
}

body {
  margin: 0;
  /* padding: 1.5rem; */
  box-sizing: border-box;
  overflow-x: hidden;
  font-family: "Poppins", sans-serif;
  background: var(--light-blue-color);
  min-height: 100vh;
  min-width: 100vw;
}

.padding {
  padding: 1.5rem;

}

/* scrollbar  */
a {
  text-decoration: none;
  display: contents;
  color: unset;
}

a:hover {
  color: inherit;
}

button {
  cursor: pointer;
  border: 0;
  background: transparent;
}

ul,
li {
  list-style: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

button:focus,
input:focus {
  outline: none;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul {
  padding: 0;
  margin: 0;
}

.d-fjcac {
  display: flex;
  justify-content: center;
  align-items: center;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

select:focus {
  outline: 0;
}

.fw-500 {
  font-weight: 500;
}

/* Global Styling End  */

/* Side bar styling  */

.sidebar-container {
  width: 200px;
  position: fixed;
  inset: 1.5rem 0 0 1.5rem;
}

.sidebar-container .logo {
  display: flex;
  align-items: center;
}

.sidebar-container .logo p {
  font-size: var(--h3-size);
  font-weight: 700;
  color: var(--primary-font-color);
  margin-left: 1.5rem;
}

/* profile start */

.sidebar-container .profile {
  display: flex;
  align-items: center;
  margin-top: 3rem;
}

.sidebar-container .profile img {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 1.5rem;
}

.sidebar-container .profile p {
  color: var(--primary-font-color);
  font-size: var(--h4-size);
  font-weight: 600;
}

.sidebar-container .profile a {
  text-decoration: underline;
  color: var(--secondary-font-color);
  font-weight: 400;
  font-size: var(--h5-size);
}

/* profile end */

/* notification & activity start */
.sidebar-container .notification {
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding-right: 1rem;
}

.sidebar-container .activity {
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.sidebar-container .notification span {
  font-size: var(--h5-size);
  font-weight: 400;
  color: var(--secondary-font-color);
  margin-left: 0.5rem;
}

.sidebar-container .notification i {
  color: var(--secondary-font-color);
  font-size: var(--h5-size);
}

.sidebar-container .indicator {
  background: var(--primary-font-color);
  color: #fff;
  width: 2rem;
  height: 1.5rem;
  text-align: center;
  border-radius: 10rem;
  font-weight: 400;
}

/* notification & activity end */

/* Navigation Start */

.sidebar-container ul {
  width: 100%;
  padding-left: 1.5rem;
}

.sidebar-container ul li {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  cursor: pointer;
}

.sidebar-container ul li h2 {
  font-size: var(--h5-size);
  color: var(--primary-font-color);
  font-weight: 600;
}

.bgWhite {
  padding: 1rem;
  width: 100%;
  margin-left: 2rem;
  border-radius: 0.5rem 0 0 0.5rem;
}

.sidebar-container ul li.active .bgWhite {
  background: #fff;
}

.sidebar-container .bgWhite .indicator {
  margin-left: auto;
}

/* Navigation End  */

/* priority start  */
.sidebar-container .prioContainer {
  margin-top: 10rem;
}

.sidebar-container .priority {
  padding-right: 1rem;
  display: flex;
  align-items: center;
  width: 100%;
  margin-bottom: 1rem;
  justify-content: space-between;
}

.sidebar-container .priority span,
.sidebar-container .logout span,
.sidebar-container .bug span {
  font-size: var(--h5-size);
  color: var(--secondary-font-color);
  font-weight: 500;
}

.sidebar-container .priority i {
  font-size: var(--h5-size);
  margin-right: 1rem;
}

.sidebar-container .priority .red {
  color: var(--red-color);
}

.sidebar-container .priority .orange {
  color: var(--orange-color);
}

.sidebar-container .priority .yellow {
  color: var(--yellow-color);
}

.sidebar-container .priority .green {
  color: var(--green-color);
}

/* priority End  */

/* Logout & Bug start */

.sidebar-container .logout i,
.sidebar-container .bug i {
  color: var(--secondary-font-color);
  font-size: var(--h5-size);
  margin-right: 0.5rem;
}

.sidebar-container .bug {
  margin-bottom: 1.5rem;
  margin-top: 4rem;
}

.sidebar-container .bug,
.sidebar-container .logout {
  border: 0;
  background: transparent;
  display: block;
}

/* Logout & Bug End */

/* Dashboard Start */

.dashboard,
/* .myProfile, */
.myworks,
.workspaces,
.settings {
  background: #fff;
  position: relative;
  left: 200px;
  width: calc(100% - 200px);
  border-radius: 1.5rem;
  padding: 1.5rem;
  min-height: calc(100vh - 3rem);
}

.workspaces {
  padding: 1.5rem 1.5rem 1.5rem 1rem;
}

.myProfile {
  background: #fff;
  position: relative;
  left: 200px;
  width: calc(100% - 200px);
  border-radius: 1.5rem;
  padding: 0;
}

.settings {
  min-height: fit-content;
  border-radius: 1.5rem 1.5rem 0 0;
}

.topNav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.topNav h1 {
  font-size: var(--h1-size);
  color: var(--primary-font-color);
  font-weight: 600;
}

.topNav .right {
  display: flex;
  align-items: center;
}

.right .input {
  background: red;
  width: 20rem;
  background: #f7f7f7;
  border-radius: 1rem;
  padding: 1rem;
  margin-right: 1.5rem;
}

.right .input input {
  border: 0;
  width: 100%;
  font-size: var(--h5-size);
  color: #939393;
  background: transparent;
  margin-left: 0.5rem;
}

.right .fa-magnifying-glass {
  color: #939393;
}

.right .btn {
  background: var(--primary-font-color);
  color: #fff;
  font-size: var(--h5-size);
  font-weight: 700;
  padding: 1rem;
  width: 10rem;
  position: relative;
  border-radius: 1rem;
}

.right .dropdown-toggle::after {
  display: none;
}

.right .dropdown .dropdown-menu a {
  font-size: var(--h5-size);
  color: var(--primary-font-color);
  font-weight: 600;
}

.right .dropdown .dropdown-menu.show {
  top: 3px !important;
}

.line {
  width: 100%;
  height: 1.5px;
  background: #eeeeee;
  margin-top: 1.5rem;
}

/* Dashboard End  */

/* Accordian Start */

.dashboard .accordion {
  margin-top: 3rem;
}

.dashboard .accordion-header button {
  font-size: var(--h3-size);
  color: var(--black-font-color);
  font-weight: 500;
  background: transparent;
  box-shadow: none;
}

.dashboard .accordion-button::after {
  position: absolute;
  left: -3px;
}

.dashboard .accordion-item {
  border: 0;
  margin-bottom: 2rem;
}

.dashboard .accordion-body {
  border: var(--border);
  min-height: 13rem;
  font-size: var(--h5-size);
  color: var(--black-font-color);
  font-weight: 500;
  border-radius: 1rem;
}

.dashboard .accordion-body .row {
  padding: 1rem;
}

.dashboard .accordion-body .boardDiv {
  border: var(--border);
  display: flex;
  align-items: center;
  padding: 1.5rem;
  margin-bottom: 1rem;
  border-radius: 1rem;
}

.dashboard .accordion-body .boardDiv h2 {
  font-size: var(--h4-size);
  color: var(--black-font-color);
  font-weight: 500;
}

.dashboard .accordion-body .boardDiv h3 {
  font-size: var(--h5-size);
  color: var(--black-font-color);
  font-weight: 400;
}

.dashboard .accordion-body .boardDiv img {
  margin-right: 1rem;
}

.dashboard .accordion-body .boardDiv .initial {
  margin-right: 1rem;
  width: 2.6rem;
  height: 2.6rem;
  border-radius: 0.5rem;
  background: var(--red-color);
  color: #fff;
  font-size: var(--h4-size);
  font-weight: 500;
}

/* Accordian End */

/* notification popup start */

.notificationPopup.modal.show {
  display: flex !important;
  justify-content: center;
  align-items: center;
}

.notificationPopup .modal-content {
  min-width: 40rem;
  min-height: 45rem;
  border: 0;
  border-radius: 1.5rem;
  padding: 1rem;
}

.notificationPopup .modal-title,
.activityPopup h5 {
  font-size: var(--h1-size);
  color: var(--primary-font-color);
  font-weight: 500;
}

.notificationPopup .btnClose,
.activityPopup .btnClose {
  background: var(--primary-font-color);
  color: #fff;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
}

.notificationPopup .fa-xmark,
.activityPopup .fa-xmark {
  font-size: var(--h4-size);
}

.notificationPopup .buttonDiv {
  display: flex;
  align-items: flex-start;
  margin-bottom: 1.5rem;
}

.notificationPopup .buttonDiv button {
  color: var(--primary-font-color);
  font-size: var(--h4-size);
  font-weight: 500;
  margin-right: 3rem;
  padding: 0.5rem 0rem;
  position: relative;
  letter-spacing: 1px;
}

.notificationPopup .buttonDiv button::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 4px;
  background: var(--primary-font-color);
  border-radius: 1rem;
  bottom: 0;
  opacity: 0;
}

.notificationPopup .buttonDiv button.active::before {
  opacity: 1;
  transition: opacity 0.3s linear;
}

.notificationPopup .notiDiv {
  background: var(--light-blue-color);
  border-radius: 0.5rem;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

.notificationPopup .notiDiv h2 {
  color: var(--primary-font-color);
  font-size: var(--h5-size);
  width: 65%;
  word-break: keep-all;
}

.notificationPopup .notiDiv .notiTab button {
  text-decoration: underline;
  color: var(--primary-font-color);
  font-size: var(--h5-size);
}

.notificationPopup .notiDiv .dot {
  background: var(--primary-font-color);
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  margin-left: 2.5rem;
}

.notificationPopup .notiDiv .notiTab {
  display: flex;
  align-items: center;
}

.animate__animated {
  animation-duration: 0.3s;
}

/* Notification popup end */

/* Activity Popup start   */
.activityPopup .offcanvas-header {
  margin-bottom: 2.5rem;
  padding-bottom: 2rem;
  border-bottom: var(--border);
}

.activityPopup {
  padding: 1rem;
}

.activityPopup .logDiv {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: 1.5rem;
}

.activityPopup .logDiv img {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
}

.activityPopup .logDiv .image span {
  font-size: var(--h5-size);
  color: var(--primary-font-color);
  font-weight: 500;
}

.activityPopup .logDiv span {
  font-size: var(--h5-size);
  color: var(--secondary-font-color);
  font-weight: 500;
  margin-left: 0.5rem;
  cursor: pointer;
}

.activityPopup .logDiv .board span {
  text-decoration: underline;
}

.activityPopup .offcanvas-body {
  padding: 0;
}

.activityPopup.offcanvas-end {
  min-width: 45rem;
}

.activityPopup .logDiv i {
  color: var(--primary-font-color-color);
  font-size: var(--h5-size);
}

/* Activity Popup end  */

/* My profile starts */
.myProfile {
  padding: 0;
}

.myProfile .top {
  border-radius: 1.5rem 1.5rem 0 0;
  min-height: 30rem;
  background: #0073ea;
  width: 100%;
  display: flex;
  justify-content: end;
  align-items: center;
  flex-direction: column;
  box-shadow: 0 3px 9px rgba(0, 0, 0, 20%);
}

.myProfile .top .icon {
  border-radius: 50%;
  width: 13rem;
  height: 13rem;
  margin-bottom: 1.2rem;
  margin-top: 5rem;
}

.myProfile .top .icon i {
  width: 100%;
  height: 100%;
  font-size: 13rem;
}

.myProfile .top .icon img {
  width: 100%;
  height: 100%;
}

.myProfile .top h1 {
  font-size: 3rem;
  color: #fff;
  margin-bottom: 3.5rem;
}

.myProfile .top ul {
  display: flex;
}

.myProfile .top ul li {
  color: #fff;
  font-size: var(--h4-size);
  margin-right: 1.5rem;
  cursor: pointer;
  position: relative;
}

.myProfile .top ul li::before {
  content: "";
  position: absolute;
  border-radius: 0.5rem;
  background: #fff;
  width: 100%;
  height: 2px;
  bottom: 0px;
  opacity: 0;
}

.myProfile .top ul li.active::before {
  opacity: 1;
  transition: opacity 0.3s;
}

.myProfile .bottom,
.bottomCus {
  min-height: calc(100vh - 33rem);
  width: 100%;
  padding-bottom: 2rem;
  padding-top: 3rem;
}

.personal {
  display: flex;
  justify-content: center;
}

.personal h1 {
  margin-bottom: 2rem;
  color: var(--black-font-color);
  font-size: var(--h1-size);
}

.personal .input {
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
}

.personal .input .icon {
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 50%;
  border: 1px solid #676879;
  margin-right: 1rem;
  padding: 2rem;
}

.personal i {
  font-size: 2.3rem;
  color: #676879;
}

.personal label {
  margin-right: 1rem;
  font-size: var(--h4-size);
  color: var(--black-font-color);
}

.personal input {
  border: 0;
  font-size: var(--h4-size);
  color: var(--black-font-color);
  padding: 0.5rem;
  border-radius: 0.3rem;
  border: 1px solid transparent;
}

.personal input:focus {
  border: 1px solid #676879;
  transition: all 0.3s;
}

.personal button {
  color: #fff;
  background: var(--primary-font-color);
  font-size: var(--h4-size);
  padding: 1rem 1.5rem;
  border-radius: 0.5rem;
  margin-top: 1rem;
}

/* Working status start  */

.myProfile .bottomCus .status h2 {
  font-size: var(--h2-size);
  color: var(--black-font-color);
  margin-bottom: 1rem;
}

.myProfile .bottomCus .status h3 {
  font-size: var(--h4-size);
  color: #676879;
}

.myProfile .bottomCus .status {
  margin-top: 3rem;
  width: 60%;
  margin-left: auto;
  margin-right: auto;
}

.myProfile .bottomCus .status .row {
  margin-top: 3rem;
}

.myProfile .bottomCus .status .row .col-md-4 {
  margin-bottom: 2rem;
  display: flex;
}

.myProfile .bottomCus .status input {
  width: 2rem;
  height: 2rem;
  margin-right: 1rem;
}

.myProfile .bottomCus .status .radioDiv {
  display: flex;
  justify-content: center;
  align-items: center;
}

.myProfile .bottomCus .status .radioDiv label {
  font-size: var(--h3-size);
  color: var(--black-font-color);
  cursor: pointer;
  margin-right: 0.5rem;
}

.myProfile .bottomCus .status .radioDiv i {
  font-size: 1.5rem;
}

.myProfile .bottomCus .status select {
  font-size: var(--h4-size);
  margin-top: 3.5rem;
  padding: 0.5rem;
  border-radius: 0.5rem;
  color: var(--black-font-color);
  margin-bottom: 1rem;
}

.myProfile .bottomCus .status .mobNotification {
  display: flex;
  align-items: center;
}

.myProfile .bottomCus .status .mobNotification label {
  font-size: var(--h4-size);
  color: var(--black-font-color);
}

.myProfile .bottomCus .status .disNotification {
  margin-top: 3.5rem;
}

.myProfile .bottomCus .status .disNotification h2 {
  font-size: 1.5rem;
  color: var(--black-font-color);
  margin-bottom: 1rem;
}

/* Working status end */

/* Password starts  */
.myProfile .bottomCus .password {
  margin-top: 3rem;
  display: flex;
  justify-content: center;
}

.myProfile .bottomCus .password h2 {
  font-size: var(--h2-size);
  color: var(--black-font-color);
  margin-bottom: 2.5rem;
}

.myProfile .bottomCus .password .input label {
  font-size: 1.5rem;
  color: var(--black-font-color);
}

.myProfile .bottomCus .password .input {
  margin-bottom: 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.myProfile .bottomCus .password .input input {
  width: 30rem;
  padding: 0.5rem;
  border-radius: 0.5rem;
  font-size: var(--h4-size);
  color: var(--black-font-color);
  background: #dcdfec;
  border: var(--border);
}

.myProfile .bottomCus .password .passDiv {
  min-width: 40%;
}

.myProfile .bottomCus .password .input a {
  font-size: var(--h5-size);
  color: var(--black-font-color);
  margin-right: 1rem;
}

.myProfile .bottomCus .password .input a.reset {
  color: #0073ea;
  margin-right: 0;
}

.myProfile .bottomCus .password .passDiv .line {
  width: 100%;
  height: 2px;
  background: #676879;
}

.myProfile .bottomCus .password .passDiv button {
  background: var(--primary-font-color);
  color: #fff;
  font-size: 1.5rem;
  padding: 0.5rem 2rem;
  margin-top: 1rem;
  display: flex;
  margin-left: auto;
}

/* Password end */

/* Language starts */
.myProfile .bottomCus .language {
  margin-top: 3rem;
  display: flex;
  justify-content: center;
}

.myProfile .bottomCus .language .input {
  margin-bottom: 2rem;
}

.myProfile .bottomCus .language .input h2 {
  font-size: var(--h2-size);
  color: var(--black-font-color);
  font-weight: 500;
  margin-bottom: 1.5rem;
}

.myProfile .bottomCus .language .input select {
  font-size: var(--h4-size);
  color: var(--black-font-color);
  padding: 0.5rem 1rem;
  width: 23rem;
  border: 1px solid #3c3f59;
  border-radius: 0.5rem;
}

.myProfile .bottomCus .language .input div {
  display: flex;
  align-items: center;
}

.myProfile .bottomCus .language .input input {
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 1rem;
}

.myProfile .bottomCus .language .input label {
  color: var(--black-font-color);
  margin-right: 1rem;
  font-size: var(--h3-size);
}

.myProfile .bottomCus .language .input span {
  color: #676879;
  font-size: var(--h4-size);
}

.myProfile .bottomCus .language button {
  background: var(--primary-font-color);
  color: #fff;
  font-size: 1.5rem;
  padding: 0.5rem 1.5rem;
  border-radius: 0.5rem;
}

/* Language end */
/* Notification start  */
.myProfile .bottomCus .notiAccordian {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
}

.myProfile .bottomCus .notiAccordian .notificationDiv {
  width: 65rem;
}

.myProfile .bottomCus .notiAccordian .notificationDiv h1 {
  font-size: var(--h2-size);
  color: var(--black-font-color);
}

.myProfile .bottomCus .notiAccordian .notificationDiv p {
  font-size: var(--h4-size);
  color: var(--black-font-color);
  font-weight: 300;
  margin-bottom: 3.5rem;
}

.myProfile .bottomCus .notiAccordian .notificationDiv .box {
  border: 1px solid #676879;
  border-radius: 0.4rem;
  display: flex;
  align-items: center;
  padding: 1.5rem 2rem;
  justify-content: space-evenly;
  margin-bottom: 3rem;
}

.myProfile .bottomCus .notiAccordian .notificationDiv h2 {
  font-size: var(--h3-size);
  color: var(--black-font-color);
}

.myProfile .bottomCus .notiAccordian .notificationDiv h3 {
  color: var(--black-font-color);
  font-size: var(--h4-size);
  font-weight: 300;
}

.myProfile .bottomCus .notiAccordian .notificationDiv i {
  font-size: 2.3rem;
  margin-right: 2rem;
}

.myProfile .bottomCus .notiAccordian .notificationDiv button {
  background: var(--primary-font-color);
  color: #fff;
  font-size: var(--h5-size);
  font-weight: 500;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
