/* Landing Page CSS  */
:root {
  --primary-font-color: #003c83;
  --secondary-font-color: #7289a3;
  --black-font-color: #323338;
  --green-color: #008e4a;
  --orange-color: #ffa53a;
  --yellow-color: #ffeb3a;
  --red-color: #ff3a3a;
  --pink-color: #f74875;
  --blue-color: #0073ea;
  --light-blue-color: #ebf5ff;
  --h1-size: 2rem;
  --h2-size: 1.8rem;
  --h3-size: 1.6rem;
  --h4-size: 1.4rem;
  --h5-size: 1.2rem;
  --h6-size: 1.5rem;
  --border: 1px solid #eeeeee;
}

header {
  width: 100%;
  position: relative;
  background: #fff;
  box-shadow: 0 4px 30px hsla(242, 100%, 83%, 0.3);
}

.navbar {
  width: 90%;
  min-height: 10vh;
  margin-inline: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: static;
  transition: all 0.2s linear;
}

.navbar ul {
  display: flex;
  align-items: center;
  gap: 2rem;
  position: relative;
  position: static;
}

.navbar .navList {
  font-size: var(--h6-size);
  font-weight: 500;
  padding: 0.8rem;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: all 0.1s linear;
}

.navbar .navList i {
  font-size: var(--h5-size);
}

.featureDrop .box .fa-solid {
  color: var(--primary-font-color);
  background: hsla(213, 100%, 26%, 0.1);
  font-size: var(--h6-size);
  padding: 0.3rem;
  border-radius: 0.1rem;
}

.navList.getStarted {
  background: var(--blue-color);
  color: #fff;
  border-radius: 100vmax;
}

.navbar .navList:hover,
.navbar .navList.active {
  background: var(--light-blue-color);
  color: var(--primary-font-color);
}

.navbar .navList.active .fa-chevron-down {
  transition: all 0.2s ease-out;
  transform: rotate(180deg);
}

.hamDiv {
  display: none;
  font-size: var(--h4-size);
  width: 90%;
  margin-inline: auto;
  align-items: center;
  justify-content: space-between;
}

.hamburger {
  font-size: var(--h1-size);
}

@media (max-width: 1110px) {
  .hamDiv {
    display: flex;
  }

  .navbar {
    position: absolute;
    background: #fff;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    top: 5rem;
    transform-origin: top center;
    transform: scaleY(0);
    visibility: hidden;
  }

  .navbar.active {
    visibility: visible;
    transform: scaleY(1);
  }

  .navbar ul {
    flex-direction: column;
  }

  .navList.getStarted,
  .navList.webLogo {
    display: none;
  }
}

/* FOOTER DESIGN  */

footer {
  background: #fff;
  width: 100%;
  position: fixed;
  bottom: 0%;
  left: 0;
  padding: 1rem;
}

.footer {
  width: 90%;
  margin-inline: auto;
  display: flex;
  justify-content: space-evenly;
}

.footer .box ul {
  display: flex;
  flex-direction: column;
}

.footer .box ul li {
  font-size: var(--h4-size);
  color: var(--black-font-color);
  font-weight: 400;
  margin-bottom: 1.5rem;
  cursor: pointer;
}

.footer .box ul li h2 {
  font-size: var(--h6-size);
  font-weight: 600;
  margin-block: 2rem;
}

.footer .box ul li img {
  margin-block: 2rem;
}

.footer .box ul li:hover {
  transition: all 0.2s linear;
  color: var(--blue-color);
}

footer .line {
  width: 80%;
  margin-inline: auto;
  height: 1px;
  background: #808080;
}

.w-80 {
  width: 80%;
}

.footer li {
  cursor: pointer;
}

.footer .left .icons i,
.footer .left .lang {
  color: var(--black-font-color);
  font-size: var(--h1-size);
  cursor: pointer;
  margin-right: 1rem;
}

.footer .terms p {
  color: #535768;
  margin-right: 1rem;
  font-weight: 500;
  font-size: var(--h4-size);
  text-decoration: underline;
  cursor: pointer;
  transition: all 0.2s linear;
}

.footer .terms p:last-child {
  text-decoration: none;
}

.footer .terms p:hover {
  color: var(--primary-font-color);
}

.lanDrop {
  display: flex;
  width: 30rem;
  flex-wrap: wrap;
  padding: 1rem;
}

.lanDrop button {
  display: block;
  font-size: var(--h4-size);
  color: var(--black-font-color);
  font-weight: 500;
  padding: 1rem;
  border-radius: 0.4rem;
  margin-right: 0.7rem;
  width: 30%;
}

.lanDrop button:hover {
  background: var(--light-blue-color);
}

/* NAVBAR DROPDROWNS  */

.featureDrop {
  background: #fff;
  padding: 2rem;
  position: absolute;
  z-index: 10;
  width: 100%;
  top: 10.2vh;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  left: 0;
  display: none;
  /* backdrop-filter: blur(5px); */
  /* position: fixed; */
  /* bottom: 0; */
}

.featureDrop.active {
  display: block;
}

.dropLanding {
  width: 90%;
  display: flex;
  margin-inline: auto;
  flex-wrap: wrap;
}

.featureDrop .box {
  border: 2px solid var(--light-blue-color);
  border-radius: 0.3rem;
  padding: 1rem;
  cursor: pointer;
  width: 28rem;
  margin-right: 2rem;
  margin-bottom: 2rem;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  transition: all 0.2s linear;
}

.featureDrop .box:hover {
  background: var(--light-blue-color);
}

.featureDrop .box h2 {
  font-size: var(--h3-size);
  color: var(--black-font-color);
  font-weight: 500;
}

.featureDrop .box p {
  font-size: var(--h5-size);
  color: #676879;
}

.resourceH3 {
  font-size: var(--h5-size);
  font-weight: 600;
  margin-bottom: 2rem;
}

.caseBox {
  border: 2px solid var(--light-blue-color);
  height: fit-content;
  border-radius: 0.5rem;
  padding: 0.8rem;
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
  max-width: 29rem;
  transition: all 0.2s linear;
}

.featureDrop .caseBox .fa-solid {
  margin-right: 1.5rem;
  font-size: var(--h3-size);
}

.caseBox h2 {
  font-size: var(--h3-size);
  color: var(--primary-font-color);
}

.caseBox.bdNone {
  border: 0;
}

.caseBox:hover {
  background: rgba(0, 115, 234, 0.2);
}

.caseBox p {
  font-size: var(--h5-size);
  color: var(--black-font-color);
  margin-top: 0.5rem;
}

.featureDrop .line {
  height: auto;
  width: 1px;
  border-radius: 0.2rem;
  background: #676879;
}

.caseBox .fa-bullhorn {
  color: var(--orange-color);
}

.caseBox .fa-code {
  color: var(--green-color);
}

.caseBox .fa-bullseye {
  color: var(--red-color);
}

.caseBox .fa-user {
  color: var(--pink-color);
}

.caseBox .fa-bars-staggered {
  color: #ffeb3a;
}

.caseBox .fa-bars-progress {
  color: #6C6CFF;
}

.caseBox .fa-arrow-trend-up {
  color: var(--blue-color);
}

.productBox {
  padding: 1.2rem;
  border-radius: 0.8rem;
  display: flex;
  margin-bottom: 4rem;
}

.productBox h2 {
  font-size: var(--h1-size);
  color: var(--black-font-color);
  font-weight: 500;
}

.productBox p {
  color: #676879;
  font-size: var(--h4-size);
  margin-top: 0.2rem;
  font-weight: 400;
}

.productBox.purple:hover {
  background: rgba(108, 108, 255, 0.1);
}

.productBox.aqua:hover {
  background: rgba(0, 115, 234, 0.1);
}

.productBox.green:hover {
  background: rgba(0, 142, 74, 0.1);
}
.productBox.pink:hover {
  background: rgba(247, 72, 117,0.1);
}
.productBox.orange:hover {
  background: rgba(255, 165, 58,0.1);
}

.productBox p span {
  font-weight: 500;
  padding: 0.2rem;
  border-radius: 0.2rem;
  margin-right: 1rem;
}

.productBox.purple p span {
  background: rgba(108, 108, 255, 0.2);
}

.productBox.aqua p span {
  background: rgba(0, 115, 234, 0.2);
}

.productBox.green p span {
  background: rgba(0, 142, 74, 0.2);
}
.productBox.pink p span {
  background: rgba(247, 72, 117,0.2);
}
.productBox.orange p span {
  background: rgba(255, 165, 58,0.2);
}